body,
div,
p,
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  display: block;
  font-family: lato;
}

header {
  background: url(../img/ColorBalance2.png) no-repeat top center / cover;
}

.clearfix:after {
  content: '';
  display: block;
  clear: both;
  width: 100%;
}

.logo {
  color: #fff;
  font-size: 25px;
  font-weight: bold;
  padding-top: 15px;
  margin-left: 60px;
  font-family: Montserrat;
}

.logo span {
  font-weight: 100;
}

flex {
  display: flex;
}

.container,
flex {
  width: 1230px;
  margin: 0 auto;
  justify-content: space-between;
}

nav {
  padding-top: 20px;
  padding-left: 25px;
  height: 139px;
  background: linear-gradient(#7ba7a7, #9bddd8 95%);
}

nav ul {
  display: block;
  list-style: none;
  text-align: right;
  flex-grow: 1;
  margin-right: 30px;
}

nav li {
  display: inline-block;
  font-family: Lato;
  font-size: 14px;
  font-weight: 800;
  text-align: center;
  padding: 15px 20px;
  margin: 0 5px;
  text-transform: capitalize;
  line-height: 14px;
}

nav li:hover {
  box-shadow: 0 0 0 3px #fff;
  border-radius: 30px;
}

nav li a {
  color: #fff;
  text-decoration: none;
}

.avatar {
  margin-top: 9px;
  margin-right: 60px;
  width: 44px;
  height: 44px;
  background-color: #ffd426;
  border-radius: 50%;
}

.avatar img {
  background: url(../img/avatar.png) no-repeat;
  border-radius: 50%;
  width: 44px;
  height: 44px;
}

.header-title {
  margin-top: 25px;
  text-align: center;
  color: #fff;
}

header .header-title-main {
  font-family: ChunkFive;
  font-size: 80px;
  font-weight: 400;
  text-transform: uppercase;
}

header .header-title-main span {
  color: #01a2a6;
}

.header-title p {
  /*    margin-top: 25px;*/
  font-size: 18px;
  font-family: lato;
  line-height: 2;
}

.button {
  text-align: center;
  margin-top: 70px;
  padding-bottom: 207px;
}

.button a {
  padding: 25px 30px;
  border-radius: 38px;
  background: #ffffa6;
  text-decoration: none;
  text-transform: uppercase;
  font-family: lato;
  font-weight: 900;
  font-style: 16px;
  color: #10273d;
}

.white-line {
  height: 45px;
  background-color: rgba(255, 255, 255, 0.9);
}

.city {
  display: inherit;
  padding: 78px 60px 109px 55px;
}

.cofl {
  line-height: 2;
  margin-left: 100px;
}

.cofl h2 {
  color: #29d9c2;
  font-family: ChunkFive;
  font-size: 60px;
  font-weight: 400;
}

.cofl h2 span {
  color: #01a2a6;
}

.city p {
  font-family: Lato;
  color: #777777;
  font-style: 16px;
}

#destination {
  background-color: #f2fefe;
}

.title {
  display: flex;
  justify-content: space-between;
  padding-top: 69px;
}

.title_main {
  max-width: 670px;
  margin-left: 100px;
}

.title_main h2 {
  font-family: ChunkFive;
  color: #29d9c2;
  font-size: 67px;
  font-weight: 400;
  padding-bottom: 40px;
}

.title_main h2 span {
  color: #01a2a6;
}

.title_main p {
  font-family: Lato;
  color: #777777;
  font-size: 16px;
  line-height: 2;
}

.title_info {
  display: flex;
  flex-grow: 1;
  align-items: flex-start;
  justify-content: center;
  margin-top: 25px;
}

.title_info info {
  align-items: flex-start;
  display: flex;
}

.title_info p {
  padding: 0 10px;
  font-family: Lato;
  color: #aeaeae;
  font-size: 16px;
  font-weight: 500;
}

.aside {
  display: flex;
  justify-content: space-between;
  padding-bottom: 19px;
}

.search_input {
  padding-left: 100px;
  padding-top: 71px;
  flex-grow: 3;
}

.search_input form {
  display: flex;
}

.search_input img {
  background-color: white;
  padding: 5px;
}

.search_input input {
  border: 1px solid #e8e8e8;
  outline: none;
}

.search_input input[type="text"] {
  border-left: none;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}

.search_input input[type="image"] {
  border-right: none;
  background-color: white;
  padding: 5px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  cursor: pointer;
}

.aside aside {
  display: flex;
  flex-grow: 1;
  align-self: flex-end;
  font-family: Lato;
  color: #aeaeae;
  font-size: 16px;
  font-weight: 900;
}

.aside aside a {
  color: #aeaeae;
  text-decoration: none;
}

.aside aside a:hover {
  color: #777777;
}

.place-big {
  position: relative;
  height: 517px;
}

.description {
  text-align: left;
  display: flex;
  justify-content: flex-end;
  position: absolute;
  top: 0;
  height: inherit;
}

.description box {
  background-color: rgba(255, 255, 255, 0.93);
  width: 30%;
  margin-right: 60px;
  padding: 53px 40px 20px 37px;
  line-height: 2;
  font-size: 16px;
}

.description box h2 {
  color: #01a2a6;
  font-size: 30px;
  font-weight: 300;
}

.description box {
  z-index: 1;
}

triangle {
  align-self: center;
  display: block;
  height: 0;
  width: 0;
  border-right: 10px solid rgba(255, 255, 255, 0.93);
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
}

.shadow {
  display: flex;
  align-self: flex-end;
  width: 309px;
  height: 108px;
  position: absolute;
  left: 46%;
  background: radial-gradient(151px 60px at bottom center, rgba(0, 0, 0, 0.5), transparent);
}

.shadow p {
  align-self: flex-end;
  padding-bottom: 10px;
  color: #fff;
  padding-left: 60px;
  cursor: pointer;
}

.shadow img {
  padding-left: 5px;
}

.sub {
  margin-top: 25px;
  margin-bottom: 25px;
  height: 364px;
  display: flex;
  flex-wrap: wrap;
}

.place-middle {
  background-color: brown;
  width: 56%;
}

.place-middle,
.place-smile {
  height: inherit;
  position: relative;
  overflow: hidden;
}

.place-smile {
  background-color: blueviolet;
  margin-left: 36px;
  width: 36.2%;
}

.desc-middle {
  height: 100%;
  width: 40%;
  background-color: rgba(255, 255, 255, 0.93);
  position: absolute;
  padding: 27px;
  line-height: 2;
  font-size: 16px;
}

.desc-middle h2 {
  color: #01a2a6;
  font-size: 30px;
  font-weight: 300;
}

.triangle {
  position: absolute;
  height: 0;
  width: 0;
  border-left: 10px solid rgba(255, 255, 255, 0.93);
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  top: 50%;
  left: 47.9%;
}

.place-middle > img,
.sub .place-smile > img {
  position: absolute;
}

.place-middle > img {
  top: -50%;
}

.link {
  opacity: 1;
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  transition: .2s;
}

.link:hover {
  opacity: 0;
  cursor: pointer;
  transition: .2s;
}

.circle {
  border-radius: 50%;
  background: url(../img/palm.png) no-repeat center, rgba(255, 255, 255, 0.9);
  width: 154px;
  height: 154px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -77px;
  margin-left: -77px;
}

.place-smile .shadow {
  left: 65%;
  bottom: 0;
}

.place-middle .shadow {
  left: 77%;
  bottom: 0;
  background: radial-gradient(151px 60px at bottom center, rgba(0, 0, 0, 0.9), transparent);
}

.menu {
  width: 100%;
  display: block;
  text-align: center;
  padding: 25px 0;
}

.menu a {
  padding: 0 2px;
  color: #777777;
  text-decoration: none;
}

.set_ittinery-title {
  text-align: center;
  padding-top: 70px;
}

.set_ittinery-title h2 {
  font-family: ChunkFive;
  color: #29d9c2;
  font-size: 67px;
  font-weight: 400;
  padding-bottom: 60px;
}

.set_ittinery-title p {
  color: #777777;
  font-size: 16px;
  line-height: 2;
}

.form {
  width: 670px;
  height: 715px;
  background-color: yellow;
}

.title-form {
  /*    height: 51px;*/
  background: url(../img/title-form.png) no-repeat center bottom;
  text-align: center;
  font-size: 18px;
  color: #fff;
  font-weight: 900;
  line-height: 75px;
}